/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.5rem;
  font-weight: 400;
  background-color: #f2f2f2;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: #3e52a3;
  font-weight: 700;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Big Shoulders Display", cursive;
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 700;
}

h1 {
  font-size: 2rem;
}

p {
  font-family: "Lexend Deca", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.columns {
  margin: 3rem auto;
}
.columns__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .columns__grid {
    margin: 16rem auto;
    max-width: 110rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.column {
  padding: 4rem;
}
.column:nth-child(1) {
  background-color: #e38826;
}
.column:nth-child(2) {
  background-color: #006970;
}
.column:nth-child(3) {
  background-color: #004241;
}
.column__image {
  width: 7rem;
}
.column__info {
  padding: 4rem 0.7rem 2rem 0;
}
@media (min-width: 1024px) {
  .column__info {
    padding: 4rem 5rem 2rem 0;
  }
}
.column__heading {
  color: #f2f2f2;
  text-transform: uppercase;
  font-size: 3.5rem;
}
.column__description {
  color: rgba(255, 255, 255, 0.75);
  font-weight: 400;
  font-size: 1.7rem;
  padding: 2rem 0 3rem 0;
}
@media (min-width: 768px) {
  .column__description {
    min-height: 30rem;
  }
}
@media (min-width: 1024px) {
  .column__description {
    min-height: 25rem;
  }
}
.column__link {
  background-color: #f2f2f2;
  padding: 1.5rem 2.5rem;
  border-radius: 4rem;
  text-decoration: none;
  font-family: "Lexend Deca", sans-serif;
  font-weight: 400;
}
.column__link:hover {
  border: 0.2rem solid #f2f2f2;
  color: #f2f2f2;
}
.column__link--Sedan {
  color: #e38826;
}
.column__link--Sedan:hover {
  background-color: #e38826;
}
.column__link--SUVs {
  color: #006970;
}
.column__link--SUVs:hover {
  background-color: #006970;
}
.column__link--Luxury {
  color: #004241;
}
.column__link--Luxury:hover {
  background-color: #004241;
}